/* Global */
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background: #fff5f5;
  color: #333;
}

.app {
  text-align: center;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #e57373;
  padding: 1rem 2rem;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 1rem;
}

.nav-links li button {
  background: none;
  border: none;
  color: white;
  font-weight: 500;
  cursor: pointer;
  transition: color 0.3s ease;
}

.nav-links li button:hover,
.nav-links li button.active {
  color: #000;
}

.menu-toggle {
  display: none;
  font-size: 1.5rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

/* Hero */
.hero {
  padding: 4rem 2rem;
}

.profile-pic {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  border: 4px solid #e57373;
  margin-bottom: 1rem;
}

.hero-title span {
  color: #e57373;
}

.hero-subtitle {
  font-size: 1.2rem;
  margin-top: 0.5rem;
}

/* Section */
.section {
  padding: 4rem 2rem;
}

.section-title {
  font-size: 2rem;
  color: #e57373;
  margin-bottom: 1rem;
}

/* Education */
.edu-list {
  list-style: none;
  padding: 0;
}

.edu-list li {
  margin: 0.5rem 0;
}

/* Skills */
.skill-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
}

.skill-list span {
  background: #e57373;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
}

/* Projects */
.project-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  justify-content: center;
}

.card {
  background: white;
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  width: 250px;
  transition: transform 0.3s ease;
}

.card:hover {
  transform: scale(1.05);
}

/* Contact */
.contact a {
  color: #e57373;
  text-decoration: none;
  font-weight: bold;
}

/* Footer */
.footer {
  background: #e57373;
  color: white;
  padding: 1rem;
  margin-top: 2rem;
}

/* Responsive */
@media (max-width: 768px) {
  .nav-links {
    display: none;
    flex-direction: column;
    background: #e57373;
    position: absolute;
    top: 60px;
    right: 20px;
    padding: 1rem;
    border-radius: 5px;
  }
  .nav-links.open {
    display: flex;
  }
  .menu-toggle {
    display: block;
  }
  .project-cards {
    flex-direction: column;
  }
}
/* Background */
.area {
  
  width: 100%;
  height: 100vh;
  position: absolute;
  z-index: -1;
}

/* Circles container */
.circles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

/* Common styles for circles */
.circle {
  position: absolute;
  display: block;
  list-style: none;
  background: red;
  bottom: -150px;
  animation: animate 25s linear infinite;
  border-radius: 50%;
}

/* Circle sizes and positions */
.circle1 {
  width: 80px;
  height: 80px;
  left: 25%;
  animation-delay: 0s;
}

.circle2 {
  width: 20px;
  height: 20px;
  left: 10%;
  animation-delay: 2s;
  animation-duration: 12s;
}

.circle3 {
  width: 20px;
  height: 20px;
  left: 70%;
  animation-delay: 4s;
}

.circle4 {
  width: 60px;
  height: 60px;
  left: 40%;
  animation-delay: 0s;
  animation-duration: 18s;
}

.circle5 {
  width: 20px;
  height: 20px;
  left: 65%;
  animation-delay: 0s;
}

.circle6 {
  width: 110px;
  height: 110px;
  left: 75%;
  animation-delay: 3s;
}

.circle7 {
  width: 150px;
  height: 150px;
  left: 35%;
  animation-delay: 7s;
}

.circle8 {
  width: 25px;
  height: 25px;
  left: 50%;
  animation-delay: 15s;
  animation-duration: 45s;
}

.circle9 {
  width: 15px;
  height: 15px;
  left: 20%;
  animation-delay: 2s;
  animation-duration: 35s;
}

.circle10 {
  width: 150px;
  height: 150px;
  left: 85%;
  animation-delay: 0s;
  animation-duration: 11s;
}

/* Animation */
@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
  }
  100% {
    transform: translateY(-1000px) rotate(720deg);
    opacity: 0;
  }
}
